$primaryColor: #4527a4;

.wp-block-bicb-carousel {
	* {
		box-sizing: border-box;
	}

	.bicbCarousel {
		display: inline-block;
		position: relative;
		overflow: hidden;
		width: 100%;
		max-width: 100%;

		.swiper-wrapper {
			.swiper-slide {
				.caption {
					display: flex;

					a {
						text-decoration: none;
					}

					&.onImage {
						width: 100%;
						height: 100%;
						position: absolute;
						top: 0;
						left: 0;
						padding: 25px;

						&.display-hover {
							opacity: 0;
							transition: .15s linear;

							p {
								opacity: 0;
								transition: .3s linear;
							}

							&:hover {
								opacity: 1;

								p {
									opacity: 1;
								}
							}
						}
					}

					&.bellowImage {
						p {
							width: 100%;
						}
					}

					p {
						margin: 0;
					}
				}


			}

			.carousel {
				width: 100%;
				height: 100%;
				margin: 0;

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}

		&.bellowImageCaption {
			.swiper-wrapper {
				padding-bottom: 26px;
			}

			.swiper-slide {
				display: flex;
				flex-direction: column;
			}
		}

		.swiper-pagination {
			.swiper-pagination-bullet {
				border-radius: 50%;

				&:focus {
					outline: 0;
				}
			}
		}

		.swiper-button-next:after,
		.swiper-button-prev:after {
			font-weight: bold;
		}
	}
}